import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import {connect} from 'react-redux'
import {loginAction} from '@redux/actions'
import Errors from '@utils/Errors'
import {Redirect} from 'react-router-dom'

class Login extends Component {

  state = {
    email: '',
    password: ''
  }

  login(e){
    e.preventDefault();
    console.log(this.state)
    this.props.loginAction(this.state)
  }

  onChange = (value, key)=>{
    this.setState({[key]:value})
  }

  render() {
    if(this.props.user.token)
    {
        return <Redirect to={
            {
                pathname: "/users",
                state: this.props.location.state
            }
        } />
    }
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="5">
                <Card className="p-4">
                  <CardBody>
                    <Errors errors={ this.props.user.error}/>
                    <Form onSubmit={(e) => this.login(e)}>
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="email" onChange={(e) => this.onChange(e.target.value, 'email')} value={this.state.email} placeholder="Email" autoComplete="" />
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input onChange={(e) => this.onChange(e.target.value, 'password')} value={this.state.password} type="password" placeholder="Password" autoComplete="current-password" />
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button type="submit" color="primary" className="px-4">Login</Button>
                        </Col>
                        <Col xs="6" className="text-right">
                          <Button color="link" className="px-0">Forgot password?</Button>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
const mapStateToProps = ({user}, ownProps) => ({user})
export default connect(mapStateToProps, {loginAction})(Login)
